*{padding:0;margin:0;border:0;box-sizing:border-box}*,:after,:before{box-sizing:border-box}:active,:focus{outline:none}a:active,a:focus{outline:none}aside,footer,header,nav{display:block}body,html{height:100%;width:100%;font-size:100%;line-height:1;font-size:14px;-ms-text-size-adjust:100%;-moz-text-size-adjust:100%;-webkit-text-size-adjust:100%}button,input,textarea{font-family:inherit}input::-ms-clear{display:none}button{cursor:pointer}button::-moz-focus-inner{padding:0;border:0}a,a:visited{text-decoration:none}a:hover{text-decoration:none}ul li{list-style:none}img{vertical-align:top}.container{display:grid;height:100vh;background-image:url("../img/bg1.jpg");background-position:center center;background-repeat:no-repeat;background-size:cover;background-attachment:fixed}.menu{display:grid;justify-items:center;align-items:center;align-self:center;grid-template:1fr/1fr 1fr 1fr}.buttons__left button:nth-child(2){margin:25% 10%;width:300px}.buttons__right button:nth-child(2){margin:25% 10%;width:300px}.button{display:flex;background-color:rgba(0,0,0,0.8);cursor:pointer;justify-content:space-around;padding:5%;font-size:25px;width:375px;text-align:center;color:#fff}.gradient-btn{transform:translate(0,0);transition:0.5s;border:2px solid #fff;border-radius:60px;overflow:hidden}.gradient-btn:before{content:"";position:absolute;top:0;left:0;width:100%;height:100%;background:linear-gradient(to left,white,red);z-index:-1;border-radius:60px;transform:scaleX(0);transform-origin:right;transition:transform 0.5s ease-in-out}.gradient-btn:hover:before{transform:scaleX(1);transform-origin:left}.logo__icon{max-width:100%}.modal{height:100vh;width:100vw;background-color:rgba(0,0,0,0.4);position:fixed;top:0;left:0;display:flex;align-items:center;justify-content:center;opacity:0;pointer-events:none;transition:0.5s}.modal.show{opacity:1;pointer-events:all}.modal__content{padding:3%;border-radius:12px;background-color:rgba(0,0,0,0.8);color:white;transform:scale(0);transition:0.5s;width:70vh;text-align:center}.modal__content.show{transform:scale(1)}.title{font-size:35px;margin-bottom:5%}.price{font-size:25px;margin-bottom:5%}.content{font-size:20px;white-space:pre-wrap;line-height:130%}a.adress{display:block;font-size:25px;color:white}a.adress:hover{background:-webkit-linear-gradient(white,red);-webkit-background-clip:text;-webkit-text-fill-color:transparent}a.tel{display:block;font-size:25px;padding:5%;color:white}a.tel:hover{background:-webkit-linear-gradient(white,red);-webkit-background-clip:text;-webkit-text-fill-color:transparent}.img__link{max-width:80px}.img__link:hover{filter:contrast(1.2)}form{display:grid;grid-template:repeat(1fr)}.text__label{display:block;font-size:20px;padding:2%}input{display:block;width:70%;margin:3% auto 0 auto;padding:3%;border-radius:5px;font-size:17px}.text__input_inline-block{display:flex;margin:0 auto 0 auto}.text__input_inline{width:42%}.select{display:block;width:70%;margin:3% auto 0 auto;padding:3%;border-radius:5px;font-size:17px}